/*jshint esversion: 6 */
import Vue from 'vue';
import Router from 'vue-router';
import archives from '@/components/page/archives';
import leftnav from '@/components/common/leftnav';
import loginSuccess from '@/components/manage/loginSuccess';
import login from '@/components/manage/login';
import articleDetails from '@/components/articleDetails/details';
import editor from '@/components/editor/editor';
import $ from 'jquery';


Vue.use(Router);

export default new Router({
  routes: [
    {
      path:'/',
      name:'archives',
      component:archives
    },
    // {
    //   path:'/category',
    //   name:'category',
    //   component:category
    // },
     {
      path: '/articleDetails',
      name: 'articleDetails',
      component: articleDetails
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/loginSuccess',
      name: 'loginSuccess',
      component: loginSuccess
    },
    {
      path: '/editor',
      name: 'editor',
      component: editor
    },
    // {
    //   path:'/aboutme',
    //   name:'aboutme',
    //   component:aboutme
    // },
    // {
    //   path:'/login',
    //   name:'login'
    // }
  ]
});



