<template>
  <div class="login">
      <form class="form-horizontal">
      <div class="control-group">
          <label class="control-label" for="inputEmail">Email</label>
          <div class="controls">
          <input type="text" id="inputEmail" placeholder="Email">
          </div>
      </div>
      <div class="control-group">
          <label class="control-label" for="inputPassword">Password</label>
          <div class="controls">
          <input type="password" id="inputPassword" placeholder="Password">
          </div>
      </div>
      <div class="control-group">
          <div class="controls">
          <label class="checkbox">
              <input type="checkbox"> Remember me
          </label>
          <router-link to="/loginSuccess"><button type="submit" class="btn">Sign in</button></router-link>
          
          </div>
      </div>
    </form>

    <div class="otherLoginWay">第三方等登录</div>

    <div class="loginWay">
      <span><i class="iconfont icon-weibo1">微博</i></span>
      <span><i class="iconfont icon-weixin1">微信</i></span>
      <span><i class="iconfont icon-qq1">QQ</i></span>
    </div>
  </div>

</template>

<style>
.container{
  /*width:85%;*/
  margin:0 auto;
  font-size:16px;
  color:#333;
}
.login{
    margin:160px auto;

}
.control-group{
    margin: 20px;
}
.controls >input{
    margin:5px;
    padding: 10px;
    border:1px solid rgba(51, 90, 104, 0.301);

    /*width: 80%;*/
}
.btn{
  background: #fff;
  border:1px solid rgba(51, 90, 104, 0.301);
}
.otherLoginWay{
  position: relative;
  margin: 50px auto;
  padding: 20px;
  border-top: 1px dashed #333;
}
.loginWay{

}
.loginWay span{
  padding: 20px 10px;
}
</style>

<script>
export default {
  name:'login',
  data(){
      return{
          msg:''
      }
  }
}
</script>





